<template>
    <div class="box">
        <input type="text" v-model="n">
        <input type="text" v-model="m">
        <button type="button" @click="responseButton">
            "click and create a maze"
        </button>
        <div>
            {{Message}}
        </div>
    </div>
</template>

<script>
import { ref } from 'vue';

export default {
    setup() {
        const n = ref(0);
        const m = ref(0);
        const Message=ref("");
        const responseButton=()=>{
            Message.value="create"+n.value+"rows,"+m.value+"cols maze";
        }
        return {
            n,
            m,
            Message,
            responseButton
        }
    },
};
</script>

<style scoped>
.box {
    height: 30vh;
}
</style>
