<template>
  <div class="box" @keyup="herospin" @keydown="heromove" tabindex="0"> </div>
  <div class="banner">
    <div class="container">
      <div class="row" v-for="i in n" :key="i">
        <div class="col" v-for="j in m" :key="j">
          <img :src="imgbackground" />
        </div>
      </div>
    </div>
    <div class="container">
      <div class="row" v-for="i in n" :key="i">
        <div class="col" v-for="j in m" :key="j">
          <img :src="images[map[j - 1][i - 1]]" />
        </div>
      </div>
    </div>
    <div class="container">
      <div class="row" v-for="i in n" :key="i">
        <div class="col" v-for="j in m" :key="j">
          <img v-if="j == hero.posx.value && i == hero.posy.value" :src="heroImage[hero.orientation.value]" />
          <img v-else :src="images[0]" />
        </div>
      </div>
    </div>
    
  </div>
  <input class="inputBox" type="number" v-model="tempMapParam.n.value"/>
  <input class="inputBox" type="number" v-model="tempMapParam.m.value"/>
  <butoon class="inputBox" @click="updateMap">click</butoon>
  <div>
    {{hero}}
    {{gameMsg}}
    {{endPos}}
  </div>
</template>

<script>
import { ref } from 'vue';
import mazeCreator from '@/assets/mazeCreator.js';
import minralPutter from '@/assets/minralPutter.js';
export default {
  setup() {
    const n = ref(11);
    const m = ref(11);
    const t=ref(0);
    const gameMsg=ref("");
    const map = ref(mazeCreator(m.value-2,n.value-2));
    const endPos=ref(minralPutter(map.value,m.value,n.value));
    const imgbackground = require("@/assets/BackGround.png");
    const images = [
      require("@/assets/TransparentPicture.png"),
      require("@/assets/Stone.png"),
      require("@/assets/Exit.png"),
      require("@/assets/BallClose.png"),
      require("@/assets/BallOpen.png"),
    ];
    const heroImage = [
      require("@/assets/img/Hero10.png"),
      require("@/assets/img/Hero11.png"),
      require("@/assets/img/Hero12.png"),
      require("@/assets/img/Hero20.png"),
      require("@/assets/img/Hero21.png"),
      require("@/assets/img/Hero22.png"),
      require("@/assets/img/Hero30.png"),
      require("@/assets/img/Hero31.png"),
      require("@/assets/img/Hero32.png"),
      require("@/assets/img/Hero40.png"),
      require("@/assets/img/Hero41.png"),
      require("@/assets/img/Hero42.png")
    ];
    const hero = {
      orientation: ref(0),
      posx: ref(endPos.value[0][0]),
      posy: ref(endPos.value[0][1])
    };
    const mapExit ={
      posx: ref(endPos.value[1][0]),
      posy: ref(endPos.value[1][1])
    };
    const tempMapParam=
    {
      n:ref(0),
      m:ref(0)
    };
    const herospin = (event) => {
      switch (event.keyCode) {
        case 65:
          hero.orientation.value=6;
          break;
        case 87:
          hero.orientation.value=0;
          break;
        case 68:
          hero.orientation.value=9;
          break;
        case 83:
          hero.orientation.value=3;
          break;
      }
    }
    const heromove = (event) => {
      t.value=1-t.value;
      switch (event.keyCode) {
        case 65:
          hero.orientation.value = 7+t.value;
          switch ((map.value[hero.posx.value - 1][hero.posy.value - 1 - 1]))
          {
            case 0:
              hero.posy.value -= 1;
              break;
            case 2:
              hero.posy.value -= 1;
              map.value[hero.posx.value - 1][hero.posy.value - 1]=0;
              gameMsg.value+="you are exit";
              break;
            case 3:
              map.value[hero.posx.value - 1][hero.posy.value - 1 - 1]=4;
              gameMsg.value+="you get minral";
          }
          break;
        case 87:
          hero.orientation.value = 1+t.value;
          switch ((map.value[hero.posx.value - 1 - 1][hero.posy.value - 1]))
          {
            case 0:
              hero.posx.value -= 1;
              break;
            case 2:
              hero.posx.value -= 1;
              map.value[hero.posx.value - 1][hero.posy.value - 1]=0;
              gameMsg.value+="you are exit";
              break;
            case 3:
              map.value[hero.posx.value - 1 - 1][hero.posy.value - 1]=4;
              gameMsg.value+="you get minral";
          }
          break;
        case 68:
          hero.orientation.value = 10+t.value;
          switch ((map.value[hero.posx.value - 1][hero.posy.value - 1 + 1]))
          {
            case 0:
              hero.posy.value += 1;
              break;
            case 2:
              hero.posy.value += 1;
              map.value[hero.posx.value - 1][hero.posy.value - 1]=0;
              gameMsg.value+="you are exit";
              break;
            case 3:
              map.value[hero.posx.value - 1][hero.posy.value - 1+1]=4;
              gameMsg.value+="you get minral";
          }
          break;
        case 83:
          hero.orientation.value = 4+t.value;
          switch ((map.value[hero.posx.value - 1 + 1][hero.posy.value - 1]))
          {
            case 0:
              hero.posx.value += 1;
              break;
            case 2:
              hero.posx.value += 1;
              map.value[hero.posx.value - 1 ][hero.posy.value - 1]=0;
              gameMsg.value+="you are exit";
              break;
            case 3:
              map.value[hero.posx.value - 1 + 1][hero.posy.value - 1]=4;
              gameMsg.value+="you get minral";
          }
          break;
      }
    };
    const updateMap=()=>{
      n.value=tempMapParam.n.value;
      m.value=tempMapParam.m.value;
      map.value = mazeCreator(m.value-2,n.value-2);
      endPos.value=minralPutter(map.value,m.value,n.value);
      hero.posx.value=endPos.value[0][0];
      hero.posy.value=endPos.value[0][1];
      mapExit.posx.value=endPos.value[1][0];
      mapExit.posy.value=endPos.value[1][1];
    };
    return {
      n,
      m,
      t,
      map,
      gameMsg,
      imgbackground,
      images,
      mapExit,
      heroImage,
      hero,
      endPos,
      tempMapParam,
      heromove,
      herospin,
      updateMap
    };
  }
};
</script>

<style scoped>
.container {
  position: absolute;
  display: flex;
  justify-content: center;
}
.inputBox{
  z-index: 2;
  position: relative;
  top:50px;
  width:50px;
}
.box {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}

.row {
  display: block;
}

.col {
  height: 32px;
}

.banner {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50vh;
}
</style>